import './AdminPedidos.css'

import {
  IonAlert,
  IonContent,
  IonDatetime,
  IonHeader,
  IonInput,
  IonItem,
  IonLabel,
  IonList,
  IonModal,
  IonTitle,
  IonToolbar,
} from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'
import Select from 'react-select'

import { Page, SDataTable } from '../../components'
import { useApiToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'

const moment = require('moment-timezone')
const numeral = require('numeral')

export const AdminRendicion: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const DataTable = React.createRef<any>()
  const [Tipo, setTipo] = useState<any>({ value: 10, label: 'Proveedor' })
  const [PersonaRendir, setPersonaRendir] = useState<any>()
  const [Fabricantes, setFabricantes] = useState<any[]>([])
  const [Transportistas, setTransportistas] = useState<any[]>([])
  const [Data, setData] = useState<any[]>([])
  const [ModalGenerarFactura, setModalGenerarFactura] = useState(false)
  const [HabilitarGenerarFactura, setHabilitarGenerarFactura] = useState(false)
  const [showAlert, setShowAlert] = useState(false)
  const [MontoFacturar, setMontoFacturar] = useState<number>(0)
  const [Ready, setReady] = useState(false)
  // Datos para generar la factura
  const [TimbradoFactura, setTimbradoFactura] = useState('')
  const [NumeroFactura, setNumeroFactura] = useState('')
  const [FechaFactura, setFechaFactura] = useState('')
  const [FilasSeleccionadas, setFilasSeleccionadas] = useState<any[]>([])
  const [TipoId, setTipoId] = useState<number>(10)
  const [PersonaRendirId, setPersonaRendirId] = useState<any>()
  const apiToast = useApiToaster()

  const setup = () => {
    obtenerFabricantes()
    obtenerTransportistas()
    setReady(true)
  }

  React.useEffect(() => {
    setup()
  }, [])

  const obtenerFabricantes = () => {
    rootStore.environment.api.fabricantesList().then((result) => {
      if (result.kind === 'ok') {
        const data = result.data.map((dat: any) => {
          return { value: dat.id, label: dat.nombre }
        })
        setFabricantes(data)
        setData(data)
      }
    })
  }

  const obtenerTransportistas = () => {
    rootStore.environment.api.transportistaList().then((result) => {
      if (result.kind === 'ok') {
        setTransportistas(
          result.data.map((dat: any) => {
            return { value: dat.id, label: dat.nombre }
          }),
        )
      }
    })
  }

  const generarFactura = () => {
    rootStore.environment.api
      .generarFacturaRendicion(
        TimbradoFactura,
        NumeroFactura,
        FechaFactura,
        FilasSeleccionadas,
        TipoId,
        PersonaRendirId,
      )
      .then((result) => {
        apiToast(result, 'Factura generada correctamente', 'generar la factura')
        if (result.kind === 'ok') {
          setModalGenerarFactura(false)
        }
      })
  }

  useEffect(() => {
    if (Ready) {
      if (Tipo && Tipo.value === 10) {
        setData(Fabricantes)
      } else {
        setData(Transportistas)
      }
      setPersonaRendir(null)
      setPersonaRendirId(null)
      DataTable && DataTable.current.refresh()
      DataTable && DataTable.current.clearSelected()
    }
  }, [Tipo])

  useEffect(() => {
    if (Ready) {
      DataTable && DataTable.current.refresh()
      DataTable && DataTable.current.clearSelected()
    }
  }, [PersonaRendir])

  useEffect(() => {
    if (!ModalGenerarFactura && DataTable && DataTable.current) {
      DataTable.current.refresh()
      DataTable.current.clearSelected()
    }
  }, [ModalGenerarFactura])

  useEffect(() => {
    if (Ready) {
      if (FilasSeleccionadas.length > 0 && PersonaRendirId !== null) {
        setHabilitarGenerarFactura(true)
        FilasSeleccionadas.map((row) => {
          const monto = row.monto + MontoFacturar
          setMontoFacturar(monto)
        })
      } else {
        setHabilitarGenerarFactura(false)
        setMontoFacturar(0)
      }
    }
  }, [FilasSeleccionadas, PersonaRendirId])

  const renderAcciones = (row: any) => {
    return (
      <div>
        <span
          className={'button'}
          title={'Visualizar'}
          onClick={() => {
            if (Tipo && Tipo.value === 10) {
              history.push('/admin/orden_venta/detail/' + row.id)
            } else {
              history.push('/admin/hoja_ruta/detail/' + row.id)
            }
          }}
        >
          <FIcon.Eye size={14} />
        </span>
      </div>
    )
  }

  return (
    <Page login_required admin solo_admin>
      <IonAlert
        isOpen={showAlert}
        onDidDismiss={() => setShowAlert(false)}
        header={'Registrar factura'}
        message={'Confirma que desea generar la factura?'}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'btn-secondary',
          },
          {
            text: 'Aceptar',
            cssClass: 'btn-primary',
            handler: () => {
              generarFactura()
            },
          },
        ]}
      />
      <IonModal
        isOpen={ModalGenerarFactura}
        backdropDismiss={true}
        onDidDismiss={() => {
          setModalGenerarFactura(false)
          setTimbradoFactura('')
          setFechaFactura('')
          setNumeroFactura('')
        }}
      >
        <IonHeader>
          <IonToolbar>
            <IonTitle> factura</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonContent>
          <IonList>
            <div className={'my-4 mx-4'}>
              <IonLabel className="ion-text-wrap">
                Factura de compra a generar a nombre de <strong>{PersonaRendir && PersonaRendir.label} </strong>con{' '}
                <strong>{FilasSeleccionadas.length} </strong>
                {FilasSeleccionadas.length === 1 ? 'item' : 'items'} por el monto de
                <strong> Gs. {numeral(MontoFacturar).format('0,0')}</strong>
              </IonLabel>
            </div>
            <IonItem>
              <IonLabel style={{ minWidth: 180 }} position="fixed">
                <strong>Número de timbrado</strong>
              </IonLabel>
              <IonInput value={TimbradoFactura} onIonChange={(e) => setTimbradoFactura(e.detail.value!)} />
            </IonItem>
            <IonItem>
              <IonLabel style={{ minWidth: 180 }} position="fixed">
                <strong>Número de factura</strong>
              </IonLabel>
              <IonInput value={NumeroFactura} onIonChange={(e) => setNumeroFactura(e.detail.value!)} />
            </IonItem>
            <IonItem>
              <IonLabel style={{ minWidth: 172 }} position="fixed">
                <strong>Fecha de la factura</strong>
              </IonLabel>
              <IonDatetime
                displayFormat="DD/M/YYYY"
                min="2021"
                max="2040"
                value={FechaFactura}
                cancelText={'Cancelar'}
                doneText={'Seleccionar'}
                placeholder={'10/10/2021'}
                onIonChange={(e) => {
                  // @ts-ignore
                  setFechaFactura(moment(e.target.value).format())
                }}
              />
            </IonItem>
          </IonList>
        </IonContent>
        <div style={{ padding: 10 }}>
          <div className={'row'}>
            <div className={'col-12 col-md-6 mb-xs-2'}>
              <button
                className={'btn btn-secondary btn-block'}
                onClick={() => {
                  setModalGenerarFactura(!ModalGenerarFactura)
                  setTimbradoFactura('')
                  setFechaFactura('')
                  setNumeroFactura('')
                }}
              >
                Cancelar
              </button>
            </div>
            <div className={'col-12 col-md-6'}>
              <button
                className={'btn btn-primary btn-block'}
                onClick={() => {
                  setShowAlert(true)
                }}
                disabled={TimbradoFactura === '' || NumeroFactura === '' || FechaFactura === ''}
              >
                Aceptar
              </button>
            </div>
          </div>
        </div>
      </IonModal>
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              <h2 className="text-themecolor">Rendición</h2>
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/')}>Inicio</a>
                </li>
                <li className="breadcrumb-item active">Rendición</li>
              </ol>
            </div>
          </div>
          <div className="card mb-0">
            <div className="card-body">
              <div className={'row'}>
                <div className={'col-4'}>
                  <h4 className={'mb-4'}>Tipo</h4>
                  <Select
                    defaultValue={Tipo}
                    onChange={(val) => {
                      setTipo(val)
                      val && setTipoId(val.value)
                    }}
                    options={[
                      { value: 10, label: 'Proveedor' },
                      { value: 20, label: 'Driver' },
                    ]}
                    theme={(theme) => ({
                      ...theme,
                      colors: {
                        ...theme.colors,
                        primary: '#303AB2',
                        neutral50: 'black',
                      },
                    })}
                  />
                </div>
                <div className={'col-4'}>
                  <h4 className={'mb-4'}>Persona a rendir</h4>
                  <Select
                    value={PersonaRendir}
                    onChange={(val) => {
                      setPersonaRendir(val)
                      val ? setPersonaRendirId(val.value) : setPersonaRendirId(null)
                    }}
                    options={Data}
                    isSearchable
                    placeholder={'Seleccionar persona a rendir'}
                    isClearable
                    theme={(theme) => ({
                      ...theme,
                      colors: {
                        ...theme.colors,
                        primary: '#303AB2',
                        neutral50: 'black',
                      },
                    })}
                  />
                </div>
              </div>
            </div>
          </div>
          <div className={'row'}>
            <div className={'col-8'}>
              <h2 className="text-themecolor my-4">Seleccionar movimientos pendientes</h2>
            </div>
            <div className={'col-4'}>
              <div className={'text-right mt-3'}>
                <div className={'btn-group ml-auto'}>
                  <div className={'text-right'}>
                    <button
                      className={'btn btn-primary'}
                      disabled={!HabilitarGenerarFactura}
                      title={'Registrar factura'}
                      onClick={() => {
                        setModalGenerarFactura(true)
                      }}
                    >
                      <FIcon.FileText size={14} style={{ marginBottom: 3 }} /> Registrar factura
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="card mb-0">
            <div className="card-body">
              <SDataTable
                ref={DataTable}
                subHeader
                selectableRows
                selectedRows={setFilasSeleccionadas}
                obtenerDatos={(perPage: number, page: number, order: any, search: string) => {
                  return rootStore.environment.api.rendicionStaff(page, perPage, order, search, TipoId, PersonaRendirId)
                }}
                columns={[
                  {
                    selector: 'id',
                    omit: true,
                    cell: (row: any) => row.id,
                  },
                  {
                    name: 'Fecha',
                    selector: 'fecha',
                    sortable: false,
                    cell: (row: any) => moment(row.fecha).format('DD/MM/YYYY HH:mm'),
                  },
                  {
                    name: 'Descripción',
                    selector: 'descripcion',
                    sortable: false,
                    cell: (row: any) => (TipoId === 10 ? 'Movimiento orden ' + row.descripcion : row.descripcion),
                  },
                  {
                    name: 'Monto',
                    selector: 'monto',
                    sortable: false,
                    cell: (row: any) => {
                      return 'Gs. ' + numeral(row.monto).format('0,0')
                    },
                  },

                  {
                    name: 'Nombre',
                    selector: 'nombre',
                    sortable: false,
                    cell: (row: any) => row.nombre,
                  },
                  {
                    name: 'Acciones',
                    sortable: false,
                    cell: (row: any) => renderAcciones(row),
                  },
                ]}
              />
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminRendicion
